@media only screen and (max-width: 768px) {
    .poweredby { 
        text-align: center; 
        line-height: 1.2em; 
        margin: 1em 0; 
    }

    .footer-navigation-column ul {
        list-style-type: none;
        padding: 0;
      }
      
      .footer-navigation-column a {
        background-color: #333;
        display: block;
        padding: .5em;
      }
}

.footer-product-summary {
    padding-right: 3em;
}

.footer-description {
    font-size: 18px;
}

.footer-navigation-column, .footer-navigation-column a  {
    color: hsla(0,0%,100%,.7);
}

.footer-navigation-column a:hover {
    color: #fff;
}